.container-nopadding {
    margin-left: auto;
    margin-right: auto;
    // outline: 1px dashed red;
    width: 100%;
  
    @media screen and (min-width: 320px) {
      width: 320px;
    }
  
    @media screen and (min-width: 768px) {
      width: 768px;
    }
  
    @media screen and (min-width: 1360px) {
      width: 1360px;
    }
  }